  <template>
    <video-player
      v-if="liveUrl"
      :sources="[{ src: liveUrl, type: 'application/x-mpegURL' }]"
      brand="jwplayer"
      :autoplay="true"
      height="auto"
      width="90%"
      :controls="true"
    />
  </template>

  <script>
  import VideoPlayer from './video/VideoPlayer'
  import moment from 'moment'
  import { mapGetters, mapActions } from 'vuex'
  // require('moment/src/locale/es.js')
  moment.locale('es')

  export default {
    name: 'AppLive',

    computed: {
      ...mapGetters([
        'liveUrl'
      ])
    },

    methods: {
      ...mapActions([
        'getDvrStores'
      ])
    },

    mounted () {
      // wowzaApi.getInstance(instance => { this.instance = instance })
    },

    created () {
      this.getDvrStores()
    },

    components: {
      VideoPlayer
    }
  }
  </script>



