<template>
  <v-layout row wrap>
    <v-flex lg6>
      <v-card v-if="$store.state.stream" height="397">
        <video-player
          :sources="[{ src: recordingUrl, type: 'application/x-mpegURL' }]"
          brand="jwplayer"
          :autoplay="true"
          :controls="true"
          :height="360"
        />
      </v-card>
      <v-progress-circular v-else indeterminate />
    </v-flex>
    <v-flex lg3>
      <v-time-picker
        v-model="selectedTime"
        :allowed-hours="allowedHours"
        :allowed-minutes="allowedMinutes"
        format="24hr"
      />
    </v-flex>
    <v-flex lg3>
      <v-date-picker
        v-model="selectedDate"
        :allowed-dates="allowedDates"
        locale="es"
      />
    </v-flex>
    <v-flex lg6>
      <v-slider label="Segundero" v-bind:max="59" v-model="currentSeconds"></v-slider>
    </v-flex>
          <v-flex lg1>
            <v-text-field v-model="currentSeconds" :prefix="secondsDisplay" type="number"></v-text-field>
          </v-flex>
    <v-flex lg6>
      <v-slider label="Duración" :max="(60 * 60 * 3)" :min="10" v-model="duration"></v-slider>
    </v-flex>
          <v-flex lg3>
            <v-text-field v-model="duration" :suffix="durationDisplay" type="number"></v-text-field>
          </v-flex>
          <v-flex lg6>
            <v-btn @click.native="convert" color="primary">Convertir</v-btn>
          </v-flex>
  </v-layout>
  </div>
</template>

<script>
// import _ from 'lodash'
import videoPlayer from './VideoPlayer'
import { mapGetters } from 'vuex'
import Moment from 'moment'
import { extendMoment } from 'moment-range'
const moment = extendMoment(Moment)

export default {
  name: 'StreamNavigator',

  data () {
    return {
      seconds: 0
    }
  },

  computed: {
    ...mapGetters([
      'recordingUrl'
    ]),
    durationDisplay () {
      return 'segundos (' + moment.duration(this.$store.state.dvrDuration, 'seconds').humanize() + ')'
    },
    secondsDisplay () {
      return this.$store.state.dvrStart.format('HH:mm:')
    },
    selectedDate: {
      get () {
        if (this.$store.state.dvrStart) {
          return this.$store.state.dvrStart.format('YYYY-MM-DD')
        }
      },
      set (val) {
        this.$store.commit('SET_DVRSTART', moment(`${val} ${this.selectedTime}:${this.currentSeconds}`, 'YYYY-MM-DD HH:mm:s'))
      }
    },

    currentSeconds: {
      get () {
        return this.$store.state.dvrStart.format('s')
      },
      set (val) {
        this.$store.commit('SET_DVRSTART', moment(`${this.selectedDate} ${this.selectedTime}:${val}`, 'YYYY-MM-DD HH:mm:s'))
      }
    },

    duration: {
      get () {
        return this.$store.state.dvrDuration
      },
      set (val) {
        this.$store.commit('SET_DVRDURATION', val)
      }
    },

    selectedTime: {
      get () {
        if (this.$store.state.dvrStart) {
          return this.$store.state.dvrStart.format('HH:mm')
        }
      },
      set (val) {
        this.$store.commit('SET_DVRSTART', moment(`${this.selectedDate} ${val}:${this.seconds}`, 'YYYY-MM-DD HH:mm:s'))
        console.log(this.$store.state.dvrStart)
      }
    }

  },

  methods: {
    allowedDates (date) {
      return Object.values(this.$store.state.dvrStoreDetails).some(store => {
        return moment(date, 'YYYY-MM-DD').range('day').overlaps(store.utcRange)
      })
    },

    allowedHours (hour) {
      return Object.values(this.$store.state.dvrStoreDetails).some(store => {
        return moment(`${this.selectedDate} ${hour}:0`, 'YYYY-MM-DD H:m').range('hour').overlaps(store.utcRange)
      })
    },

    allowedMinutes (minute) {
      if (this.selectedDate && this.selectedTime) {
        const [ hour, , ] = this.selectedTime.split(':')
        return Object.values(this.$store.state.dvrStoreDetails).some(store => {
          return store.utcRange.contains(moment(`${this.selectedDate} ${hour}:${minute}`, 'YYYY-MM-DD HH:m'))
        })
      }
      return true
    },

    convert () {
      console.log('connn')
    }
  },

  created () {
    setTimeout(() => {
      this.currentDate = moment(new Date())
      this.maxHour = this.currentDate.hours()
      this.maxMinute = this.currentDate.minutes()
    }, 1000)
  },

  watch: { },

  components: {
    videoPlayer: videoPlayer
  }
}
</script>

<style>
  .video-player {
    width: 100%;
  }
</style>



