<template>
    <v-layout>
      <StreamNavigator />
    </v-layout>
</template>

<script>
import StreamNavigator from './video/StreamNavigator'
import moment from 'moment'
import { mapGetters, mapActions } from 'vuex'
// require('moment/src/locale/es.js')
moment.locale('es')

export default {
  name: 'AppRecorder',

  computed: {
    ...mapGetters([
      'streams',
      'dvrStores'
    ])
  },

  methods: {
    ...mapActions([
      'getDvrStores'
    ])
  },

  created () {
    this.getDvrStores()
  },

  components: {
    StreamNavigator
  }
}
</script>



