export const BRAND = 'html5'

export default {

  created () {
    if (this.brand === BRAND) {
      this.dependenciesLoaded = {
        hlsjs: false
      }
      this.injectJs('https://cdn.jsdelivr.net/npm/hls.js@latest', () => {
        this.dependenciesLoaded.hlsjs = true
      })
    }
  },

  mounted () {
    if (this.brand === BRAND) {
      // when all dependencies are loaded, load HLS media
      this.$watch('allDependenciesLoaded', (allLoaded) => {
        if (allLoaded) {
          this.$nextTick(() => {
            if (!this.hls && window.Hls.isSupported()) {
              this.hls = new window.Hls()
              this.hls.loadSource(this.hlsSource.src)
              this.hls.attachMedia(this.$refs.player)
              this.hls.on(window.Hls.Events.MANIFEST_PARSED, () => {
                if (this.autoplay) {
                  this.$refs.player.play()
                }
              })
            }
          })
        }
      })
      // when sources change, realod media
      this.$watch('hlsSource', (source) => {
        console.log(source)
        if (this.hls && source.src) {
          this.hls.detachMedia()
          this.hls.attachMedia(this.$refs.player)
          this.hls.loadSource(source.src)
        }
      })
    }
  }
}



