/* Actions */
import wowzaApi from '@/api/wowza'
import Moment from 'moment'
import { extendMoment } from 'moment-range'
const moment = extendMoment(Moment)

export default {

  getDvrStores: ({ commit, state, getters, dispatch }) => {
    wowzaApi.getStores(dvrStores => {
      commit('RECEIVE_DVRSTORES', dvrStores)
      // select first stream if none selected
      if (state.stream == null && getters.streams.length) {
        dispatch('selectStream', getters.streams[0])
      }
    })
  },

  selectStream: ({ commit, state, getters, dispatch }, stream) => {
    commit('SELECT_STREAM', stream)

    commit('RESET_DVRSORE_DETAILS')
    Promise.all(state.dvrStores[stream].map(dvrStore => {
      return dispatch('getDvrStoreDetails', dvrStore)
    })).then(() => {
      commit('COMPLETE_DVRSORE_DETAILS')
      if (!state.dvrStart) {
        commit('SET_DVRSTART', moment(getters.dvrAvailableMax).subtract(5, 'minutes').seconds(0))
      }
    })
  },

  getDvrStoreDetails: ({ commit }, dvrStore) => {
    return wowzaApi.getStoreDetails(dvrStore, details => {
      commit('RECEIVE_DVRSTORE_DETAILS', { dvrStore, details })
    })
  }

}



